<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  name: "LineChart",
  props: ["data"],
  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          label: "Execution Time (sec)",
          backgroundColor: "#04D99D",
          data: [],
          type: 'line',
          order: 2
        },
        {
          label: "Error",
          backgroundColor: "red",
          data: [],
          order: 0
        }
      ]
    };

    for (let i = 0; i < this.data.length; i++) {
      chartData.labels.push(this.data[i].label);
      chartData.datasets[0].data.push(this.data[i].executionTime);
      chartData.datasets[1].data.push(this.data[i].status);
    }

    this.renderChart(chartData, {
      responsive: true,
      maintainAspectRatio: false
    });
  }
};
</script>