<template>
  <div id="dashboard" class="dashboard">
    <div class="selection">
      <div class="inputdiv">
        <h6> Client </h6>
        <span>
          <select name="type" v-model="clientName" ref="clientselect" @change="selectClientUserName">
            <option v-for="(item,index) in clients" :key="index" >{{item.name}}</option>
          </select>
        </span>
      </div>
      <div class="inputdiv">
        <h6> Portfolio </h6>
        <span>
          <select name="type" v-model="portfolioName" ref="portfolio" @change="selectPortfolioId">
            <option v-for="(item,index) in portfolios" :key="index" >{{item.displayName}}</option>
          </select>
        </span>
      </div>
      <div class="inputdiv">
        <span class="large">
          <input list="browsers" name="browser" placeholder="Search for Technical Id" v-model="portfolioid" @change="selectPortfolioName"/>
            <datalist id="browsers" >
              <option v-for="(item,index) in portfolios" :key="index">{{item.collectionId}}</option>
            </datalist>
        </span>
      </div>
    </div>

     <div class="selection2">
      <div class="inputdiv">
        <h6> API </h6>
        <span>
          <select name="type" v-model="apiName" ref="apiselect" @change="selectApiId">
            <option v-for="(item,index) in apilist" :key="index" >{{item}}</option>
          </select>
        </span>
      </div>
    </div>

    <div class="content">
      <div class="chart">
        <LineChart v-if="loaded" :data="chartData" :styles="styles"/>
      </div>
    </div>

  </div>
</template>

<script>
import LineChart from "@/components/LineChart"
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "dashboard",
  components: { LineChart },
  data() {
    return {
      clientName: '',
      clientid: '',
      clients: [],
      portfolioName:'',
      portfolioid:'',
      portfolios: [],
      apiName: '',
      apilist: ['quickinfo','cluster','grid','region','locations'],

      loaded: false,
      chartData:  [],
      styles: {
        width: "1000px",
        height: "400px",
        position: "relative"
      }

    };
  },

  watch: {},
  methods: {
    selectApiId()
    {
      this.loadkpi();
    },
    selectClientUserName()
    {
      this.clientid = this.clients[this.$refs.clientselect.selectedIndex].id;
      this.loadPortfolios(this.clients[this.$refs.clientselect.selectedIndex].clientid);
      this.portfolioid = '';
      this.portfolioName = '';
      this.loadkpi()
    },
    selectPortfolioId()
    {
       this.portfolioid = this.portfolios[this.$refs.portfolio.selectedIndex].collectionId;
        this.loadkpi()
    },
    selectPortfolioName()
    {
        this.portfolioName = this.portfolios.filter(x => x.collectionId === this.portfolioid)[0].displayName
        //alert(this.portfolios.filter(x => x.collectionId === this.portfolioid)[0].displayName)
    },
    /*  showpopup() {
      EventBus.$emit("Popup", {
        popupType: "warning"
      });
    } */
    loadClients() {
      https.get("/api/configuration/usermgmt.xsjs?user=list").then(
        resolve => {
          resolve.data.forEach(element => {
            this.clients.push(element);
          });
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error",
            message: error
          });
        }
      );
    },

    loadPortfolios(clientid){
      https
      .get(
        "/api/configuration/portfolio.xsjs?type=portfolio&clientid=" +clientid
      )
      .then(
        resolve => {
          resolve.data.forEach(element => {
            this.portfolios.push(element);
          });
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching portfolios",
            message: error
          });
        }
      );
    },
    loadkpi()
    {
      https
      .get(
        `/api/configuration/joblogs.xsjs?client=${this.clientid ? this.clientid : ''}&portfolio=${this.portfolioid ? this.portfolioid : '' }&api=${this.apiName ? this.apiName : '' }`
      )
      .then(
        async resolve => {
          this.loaded = false;
          let temp = [];
          this.chartData = [];
          await Object.values(resolve.data).forEach(item => {
            this.chartData.push({
                "user" : item.USER_ID,
                "label": item.LABEL,
                "executionTime": parseFloat(item.TIMETAKEN),
                "status" : item.STATUS === 200 ? 0 : 1
              });
              temp.push(parseFloat(item.TIMETAKEN))
          });
          this.chartData.reverse();
  //        let mx = Math.max(...temp);
//          this.chartData.map(x => x.status = x.status === 1 ? mx : 0)
          this.loaded = true;
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching portfolios",
            message: error
          });
        }
      );
    }
  },
  mounted(){
    this.loadClients();
    this.loadkpi()
  },
  created() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

.selection, .selection2{
  margin-top: 50px;
  margin-left: 50px;
  height: 50px;
  display: flex
}
.selection2{
  margin-top: 10px
}
.inputdiv{
    display: flex;
    align-content: center;
    height: 30px;
    margin-left: 10px;
    margin-right: 10px
  }

  .inputdiv h6{
    font-size: 1em;
    font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
    margin-right: 10px;
    font-weight: 590;
    letter-spacing: 0.01em;
    display: flex;
    margin-top: 5px;
  }

  .inputdiv span{
    font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
    margin-right: 10px;
    font-weight: 500;
    height: 30px;
    min-width: 250px;
    flex-basis: 1;
    display: flex;
  }

  .inputdiv span::before{
    content: ":";
    display: inline-block;
    margin-left: 5px;
    margin-right: 10px;
    margin-top: 5px;
  }

select {
  font-size: 1em;
  letter-spacing: 0.05em;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  outline: none;
  text-overflow: ellipsis;
  overflow: hidden;
  border: 0px;
  border-bottom: 2px solid;
  width: 100%;
}

.large{
  width: 300px;
}

input {
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  outline: none;
  text-overflow: ellipsis;
  overflow: hidden;
  border: 0px;
  border-bottom: 2px solid;
  width: 100%;
}

option{
  font-size: 0.9em;
  font-family: "IBM Plex Sans";
}

.content{
  display: flex;
  justify-content: space-between;
  margin-left: 50px;
  margin-top: 30px;
}

.apiSelection{
    width: 200px;
    margin-right: 10px; 
}

.chart {
    max-width: 100%;
  }

</style>
